import React, { useState, useEffect } from 'react';
import { BarChart3, Clock, CheckCircle, AlertCircle, TrendingUp, History } from 'lucide-react';
import { taskAPI } from '../services/api';
import { TaskHistory } from '../types';

const Analytics: React.FC = () => {
  const [analytics, setAnalytics] = useState<any>(null);
  const [history, setHistory] = useState<TaskHistory[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchAnalytics();
    fetchHistory();
  }, []);

  const fetchAnalytics = async () => {
    try {
      const data = await taskAPI.getAnalytics();
      setAnalytics(data);
    } catch (error) {
      // Analytics failure shouldn't break the app
      setAnalytics({ avg: 0, trend: [] });
    }
  };

  const fetchHistory = async () => {
    try {
      const data = await taskAPI.getTaskHistory();
      setHistory(data.slice(0, 5)); // Show last 5 activities
    } catch (error) {
      // History failure shouldn't break the app
      setHistory([]);
    } finally {
      setLoading(false);
    }
  };


  if (loading) {
    return (
      <div className="space-y-6">
        <div className="animate-pulse">
          <div className="bg-white dark:bg-gray-800 p-4 rounded-lg">
            <div className="h-4 bg-gray-200 dark:bg-gray-700 rounded w-3/4 mb-3"></div>
            <div className="h-8 bg-gray-200 dark:bg-gray-700 rounded"></div>
          </div>
        </div>
      </div>
    );
  }


  return (
    <div className="bg-white dark:bg-gray-800 rounded-lg p-4 md:p-6 shadow-sm">
      <div className="flex items-center gap-3 mb-6">
        <BarChart3 className="h-6 w-6 text-blue-500" />
        <h3 className="text-xl font-bold text-gray-900 dark:text-white">
          Analytics Overview
        </h3>
      </div>

      {/* Responsive Grid Layout */}
      <div className="grid grid-cols-1 lg:grid-cols-4 gap-6">
        
        {/* Average Completion Time */}
        {analytics && (
          <div className="bg-gradient-to-r from-blue-50 to-indigo-50 dark:from-blue-900/20 dark:to-indigo-900/20 p-4 rounded-lg">
            <div className="flex items-center justify-between lg:flex-col lg:items-center lg:text-center">
              <span className="text-sm lg:text-base text-gray-700 dark:text-gray-300 flex items-center gap-2 lg:flex-col">
                <Clock className="h-5 w-5" />
                <span>Average Completion Time</span>
              </span>
              <span className="text-2xl lg:text-3xl font-bold text-blue-600 dark:text-blue-400 lg:mt-2">
                {analytics.avg}min
              </span>
            </div>
          </div>
        )}

        {/* Task Summary Stats */}
        <div className="bg-gradient-to-r from-green-50 to-emerald-50 dark:from-green-900/20 dark:to-emerald-900/20 p-4 rounded-lg">
          <div className="flex items-center justify-between lg:flex-col lg:items-center lg:text-center">
            <div className="flex items-center gap-2 lg:flex-col">
              <CheckCircle className="h-5 w-5 text-green-500" />
              <span className="text-sm lg:text-base text-gray-700 dark:text-gray-300">Completed</span>
            </div>
            <div className="text-2xl lg:text-3xl font-bold text-green-600 dark:text-green-400 lg:mt-2">
              {analytics?.completedTasks || 0}
            </div>
          </div>
        </div>

        <div className="bg-gradient-to-r from-orange-50 to-amber-50 dark:from-orange-900/20 dark:to-amber-900/20 p-4 rounded-lg">
          <div className="flex items-center justify-between lg:flex-col lg:items-center lg:text-center">
            <div className="flex items-center gap-2 lg:flex-col">
              <AlertCircle className="h-5 w-5 text-orange-500" />
              <span className="text-sm lg:text-base text-gray-700 dark:text-gray-300">Pending</span>
            </div>
            <div className="text-2xl lg:text-3xl font-bold text-orange-600 dark:text-orange-400 lg:mt-2">
              {analytics?.pendingTasks || 0}
            </div>
          </div>
        </div>

        {/* Recent Activity Summary */}
        <div className="bg-gradient-to-r from-purple-50 to-violet-50 dark:from-purple-900/20 dark:to-violet-900/20 p-4 rounded-lg">
          <div className="flex items-center justify-between lg:flex-col lg:items-center lg:text-center">
            <div className="flex items-center gap-2 lg:flex-col">
              <History className="h-5 w-5 text-purple-500" />
              <span className="text-sm lg:text-base text-gray-700 dark:text-gray-300">Recent Activity</span>
            </div>
            <div className="text-2xl lg:text-3xl font-bold text-purple-600 dark:text-purple-400 lg:mt-2">
              {history.length}
            </div>
          </div>
        </div>
      </div>

      {/* Productivity Trend Chart - Full Width */}
      {analytics && analytics.trend && analytics.trend.length > 0 && (
        <div className="mt-6 bg-gray-50 dark:bg-gray-700/30 p-4 md:p-6 rounded-lg">
          <div className="text-lg font-medium text-gray-700 dark:text-gray-300 mb-4 flex items-center gap-2">
            <TrendingUp className="h-5 w-5" />
            Productivity Trend (Sliding Window)
          </div>
          <div className="flex items-end gap-2 md:gap-3 h-32 md:h-48 bg-white dark:bg-gray-800 p-3 md:p-6 rounded-lg shadow-inner overflow-x-auto">
            {analytics.trend.map((value: number, index: number) => {
              const maxValue = Math.max(...analytics.trend);
              const minValue = Math.min(...analytics.trend);
              const range = maxValue - minValue || 1;
              const normalizedHeight = maxValue > 0 ? ((value - minValue) / range) * 80 + 20 : 20;
              
              return (
                <div key={index} className="flex-1 flex flex-col items-center justify-end h-full min-w-[40px]">
                  <div
                    className="bg-gradient-to-t from-blue-500 to-blue-400 hover:from-blue-600 hover:to-blue-500 rounded-t-lg w-full transition-all cursor-pointer min-h-[12px] shadow-sm"
                    style={{
                      height: `${normalizedHeight}%`,
                    }}
                    title={`Period ${index + 1}: ${value}min`}
                  />
                  <div className="text-xs md:text-sm font-medium text-gray-600 dark:text-gray-400 mt-2 text-center">
                    {index + 1}
                  </div>
                </div>
              );
            })}
          </div>
          <div className="text-xs md:text-sm text-gray-600 dark:text-gray-400 mt-3 text-center font-medium">
            Time periods (most recent data)
          </div>
        </div>
      )}

      {/* Recent Activity Details - Responsive */}
      <div className="mt-6 bg-gray-50 dark:bg-gray-700/30 p-4 md:p-6 rounded-lg">
        <div className="flex items-center gap-2 mb-4">
          <History className="h-5 w-5 text-purple-500" />
          <h4 className="font-semibold text-gray-900 dark:text-white">
            Recent Activity Details
          </h4>
          <span className="text-xs text-gray-500 dark:text-gray-400">(Linked List)</span>
        </div>

        <div className="space-y-3 max-h-48 md:max-h-64 overflow-y-auto">
          {history.length > 0 ? (
            history.map((item, index) => {
              const getActionColor = (action: string) => {
                switch (action.toLowerCase()) {
                  case 'created': return 'bg-green-500';
                  case 'updated': return 'bg-blue-500';  
                  case 'deleted': return 'bg-red-500';
                  case 'completed': return 'bg-purple-500';
                  default: return 'bg-gray-500';
                }
              };
              
              const getActionIcon = (action: string) => {
                switch (action.toLowerCase()) {
                  case 'created': return '+';
                  case 'updated': return '';
                  case 'deleted': return '';
                  case 'completed': return '';
                  default: return '';
                }
              };

              return (
                <div key={index} className="flex items-start gap-3 text-sm">
                  <div className={`w-6 h-6 ${getActionColor(item.action)} rounded-full flex items-center justify-center text-white text-xs font-bold flex-shrink-0`}>
                    {getActionIcon(item.action)}
                  </div>
                  <div className="flex-1 min-w-0">
                    <span className="text-gray-900 dark:text-white capitalize">
                      Task {item.action.toLowerCase()}
                    </span>
                    <div className="text-gray-500 dark:text-gray-400 text-xs">
                      {new Date(item.timestamp).toLocaleTimeString()}  {new Date(item.timestamp).toLocaleDateString()}
                    </div>
                  </div>
                </div>
              );
            })
          ) : (
            <div className="text-center text-gray-500 dark:text-gray-400 py-4">
              No recent activity
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default Analytics;